import React, { Component } from 'react';
import { history } from '@/utils/history';
import { FormattedMessage } from 'react-intl';
import "./index.scss"
import { Xfn } from '../../../../utils/axiosfn';
class Tibiyang extends Component {
  constructor() {
    super()
    this.state = {
      tibirenzheng: {}
    }
  }
  componentDidMount() {
    Xfn({
      _u: "authrenzz",
      _m: "get",
      _p: {
        time: new Date().getTime().toString()
      }
    }, (res, code) => {
      if (code == 0) {
        const daa = res.data.data
        this.setState({
          tibirenzheng: daa
        })
        if (daa.fund_pwd_auth != "0" && daa.identity_auth != "0" && daa.short_msg_auth == "1") {
          history.push("/finance/tibi/sd")
        }
      }
    })
  }
  render() {
    const { tibirenzheng } = this.state
    return (
      <div className="tibiyang-warp">
        <div className="asset-title">
          <h1><FormattedMessage id="Withdraw_money" defaultMessage={''} /></h1>
        </div>
        <div className="content-tibiyang">
          <div>
            <FormattedMessage id="sake_of_your_account_security" defaultMessage={''} />
          </div>
          <div>
            <div className="tibi-box clear">
              <div className="biaoji">
                1
              </div>
              <div className="fengexian">

              </div>
              <div className="content">
                <div><FormattedMessage id="identity_authentication" defaultMessage={''} /></div>
                <div><FormattedMessage id="Completing_personal_certification" defaultMessage={''} /></div>
              </div>
              {
                (() => {
                  if (tibirenzheng.identity_auth == "0") {
                    return (<div className="renzheng" onClick={() => history.push("/personal/grsecurity/index")}>
                      <FormattedMessage id="De_certification" defaultMessage={''} />
                    </div>)
                  } else {
                    return <div className="bangding">
                      <FormattedMessage id="Certified" defaultMessage={''} />
                    </div>
                  }
                })()
              }

            </div>
            <div className="tibi-box clear">
              <div className="biaoji">
                2
              </div>
              <div className="fengexian">

              </div>
              <div className="content">
                <div><FormattedMessage id="Set_SMS" defaultMessage={''} /></div>
                <div><FormattedMessage id="Used_to_collect_verification_information" defaultMessage={''} /></div>
              </div>
              {
                (() => {
                  if (tibirenzheng.short_msg_auth == "0") {
                    return <div className="renzheng" onClick={() => history.push('/personal/security/szphone')}>
                      <FormattedMessage id="To_set_up" defaultMessage={''} />
                    </div>
                  } else {
                    return <div className="bangding">
                      <FormattedMessage id="Set_up_ed" defaultMessage={''} />
                    </div>
                  }
                })()
              }


            </div>
            {/* <div className="tibi-box clear">
              <div className="biaoji">
                3
              </div>
              <div className="fengexian">

              </div>
              <div className="content">
                <div></div>
                <div></div>
              </div>

              {
                (() => {
                  if (tibirenzheng.mail_auth == "0") {
                    return <div className="renzheng" onClick={() => history.push('/personal/security/szemail')}>
                      
                  </div>
                  } else {
                    return <div className="bangding">
                      
                    </div>
                  }
                })()
              }
            </div> */}
            <div className="tibi-box clear">
              <div className="biaoji">
                3
              </div>
              <div className="fengexian">

              </div>
              <div className="content">
                <div><FormattedMessage id="Setting_up_fund_password" defaultMessage={''} /></div>
                <div><FormattedMessage id="Use_in_dealing_and_withdrawing_money" defaultMessage={''} /></div>
              </div>
              {
                (() => {
                  if (tibirenzheng.short_msg_auth == "1" && tibirenzheng.identity_auth == "1") {
                    if (tibirenzheng.fund_pwd_auth == "0") {
                      return <div className="renzheng" onClick={() => history.push('/personal/security/zjpass')}>
                        <FormattedMessage id="To_set_up" defaultMessage={''} />
                      </div>
                    } else {
                      return <div className="bangding">
                        <FormattedMessage id="Set_up_ed" defaultMessage={''} />
                      </div>
                    }
                  } else {
                    return <div className="panduan">
                      {/* <FormattedMessage id="Please_complete_step_2_first" defaultMessage={'2'} /> */}
                      
                    </div>
                  }
                })()
              }
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Tibiyang;