import React, { Component } from 'react';
import Header from '@/scripts/components/header';
import Footer from '@/scripts/components/footer';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { history } from '@/utils/history'
import { Button, message, Modal } from 'antd';
import './index.scss'
import TableForm from '../../components/tableForm';
import { dangqianchipang } from '@/utils/dangqianchipang'
import ShareImage from '../../components/shareImage'
import { timehuansuan } from '../../../utils/time';
import { Xfn } from '../../../utils/axiosfn';
// import EventFN from '../../../utils/eventfn';
import { FormattedMessage } from 'react-intl';
import lang from '@/utils/language';
import { openNotificationWithIcon } from '../../../utils/NotificationCONF';
class fanyongBanner extends Component {
  constructor() {
    super()
    this.state = {
      imgArr: {
        a1: require('../../img/fanyong_banner.png'),
        a2: require('../../img/fanyong_buzou01.png'),
        a3: require('../../img/fanyong_buzou02.png'),
        a4: require('../../img/fanyong_buzou03.png'),
        a5: require('../../img/fanyong_share_imgicon.png'),
        a6: require('../../img/fanyong_bangdan_no1.png'),
        a7: require('../../img/fanyong_bangdan_no2.png'),
        a8: require('../../img/fanyong_bangdan_no3.png'),
        a9: require('../../img/share_bg_img.png'),
        a10: require('../../img/share_close.png'),
      },
      address: '--',
      addresse: "",
      getInviteUser: [],
      getSortRakeBack: [],
      dataLength: "",
      pictureSharing: false,
      columns: [
        {
          title: lang().Account_number_of_invited_person,
          dataIndex: 'account_id',
          render: (text, record) => <div className="zititr" style={{ wordWrap: "break-word" }}>
            {text}
          </div>,
        },
        {
          title: lang().time,
          dataIndex: 'time',
          render: (text, record) => <div className="zititr" style={{ wordWrap: "break-word" }}>

            {timehuansuan(text).date}
            &ensp;
              {timehuansuan(text).dates}

          </div>,
        },
        {
          title: lang().Direct_indirect,
          dataIndex: 'level',
          render: (text, record) => <div className="zititr" style={{ wordWrap: "break-word" }}>
            {text == "1" ? lang().Direct : lang().Indirect}
          </div>,
        },
        {
          title: lang().state,
          dataIndex: 'rake_back_status',
          align: "right",
          render: (text, record) => <div className="zititr" style={{ wordWrap: "break-word" }}>
            {text == "1" ? lang().Return_commission : lang().No_return}
          </div>,
        },
      ],
      columns1: [
        {
          title: lang().Commission,
          dataIndex: 'rake_back_amt',
          render: (text, record) => <div className="zititr" style={{ wordWrap: "break-word" }}>
            {text}
          </div>,
        },
        {
          title: lang().Payment_time,
          dataIndex: 'time',
          align: "right",
          render: (text, record) => <div className="zititr" style={{ wordWrap: "break-word" }}>
            {timehuansuan(text).date}
            &ensp;
            {timehuansuan(text).dates}
          </div>,
        },
      ],
      getRakeBack: [],
      getRakeBacklength: "",
      sumAmt: "",
      nian: "",
      yue: ""
    }
  }
  componentDidMount() {
    const bodys = document.getElementsByTagName("body")[0]
		bodys.className = "theme-light"
    var dat = new Date()
    this.setState({
      nian: dat.getFullYear(),
      yue: dat.getMonth() + 1,
    })
    if (localStorage.userInfo) {
      Xfn({
        _u: "getInviteUser",
        _m: "get",
        _p: {
          current_page: "1",
          page_size: "10",
          time: new Date().getTime().toString()
        }
      }, (res, code) => {
        this.setState({ getInviteUser: res.data.data.rows, dataLength: res.data.data.total })
      })
      Xfn({
        _u: "getReferrerCode",
        _m: "get",
        _p: {
          time: new Date().getTime().toString()
        }
      }, (res, code) => {
        this.setState({
          address: res.data.data.referrer_url,
          addresse: res.data.data.referrer_code
        })
      })
      Xfn({
        _u: "getRakeBack",
        _m: "get",
        _p: {
          page_size: "10",
          current_page: "1",
          time: new Date().getTime().toString()
        }
      }, (res, code) => {
        this.setState({ getRakeBack: res.data.data.rows, getRakeBacklength: res.data.data.total, sumAmt: res.data.data.sum_amt })
      })
    }
    Xfn({
      _u: "getSortRakeBack",
      _m: "get",
      _p: {
        count: "10",
        time: new Date().getTime().toString()
      }
    }, (res, code) => {
      this.setState({ getSortRakeBack: res.data.data.rows })
    })

  }
  fenyed = (_obj) => {
    Xfn({
      _u: "getRakeBack",
      _m: "get",
      _p: {
        current_page: _obj,
        page_size: "10",
        time: new Date().getTime().toString()
      }
    }, (res, code) => {
      this.setState({ getRakeBack: res.data.data.rows, getRakeBacklength: res.data.data.total, sumAmt: res.data.data.sum_amt })
    })
  }
  fenyeds = (_obj) => {
    Xfn({
      _u: "getInviteUser",
      _m: "get",
      _p: {
        current_page: _obj,
        page_size: "10",
        time: new Date().getTime().toString()
      }
    }, (res, code) => {
      this.setState({ getInviteUser: res.data.data.rows, dataLength: res.data.data.total })
    })
  }
  pictureSharing = () => {
    this.setState({ pictureSharing: true })
  }
  pictureSharingCancel = () => {
    this.setState({ pictureSharing: false })
  }
  pictureSharingOk = () => {
    this.refs.shareimg.ClickDownLoad()
    this.setState({ pictureSharing: false })
  }
  render() {
    const { imgArr, address, addresse, getSortRakeBack, getInviteUser, columns, dataLength, columns1, getRakeBack, getRakeBacklength, sumAmt } = this.state
    return (
      <div className="fanyong-annerwarp">
        <Header></Header>
        <article>
          <section>
            <img className="imga1" src={imgArr.a1} alt="" />
          </section>
          <section>
            <div className="wPHz-box clear">
              <div className="conste-p1 clear">
                <img src={imgArr.a2} alt="" />
                <p className="spans-cos1">
                  <span>
                    < FormattedMessage id="Send_invitations_to_friends" defaultMessage={''} />
                  </span>
                  <span>
                    < FormattedMessage id="Invite_friends_to_register_by_sharing_link" defaultMessage={''} />
                    GTE</span>
                </p>
                <div className="br-bordayangshi">

                </div>
              </div>
              <div className="conste-p1">
                <img src={imgArr.a3} alt="" />
                <p className="spans-cos1">
                  <span>< FormattedMessage id="Friend_registration" defaultMessage={''} /></span>
                  <span>< FormattedMessage id="Friends_accept_the_invitation" defaultMessage={''} /></span>
                </p>
                <div className="br-bordayangshi">

                </div>
              </div>
              <div className="conste-p1">
                <img src={imgArr.a4} alt="" />
                <p className="spans-cos1">
                  <span>< FormattedMessage id="Get_corresponding_proportion_of_commission" defaultMessage={''} /></span>
                  <span>< FormattedMessage id="Easy_access_to_transaction" defaultMessage={''} /></span>
                </p>
              </div>
              <div className="position-box">
                <div className="my-yaoqingma clear">
                  <div className="span-my-1">
                    < FormattedMessage id="My_exclusive_invitation_code" defaultMessage={''} />
                    :
                    </div>
                  <div className="span-my-2">
                    {
                      (() => {
                        if (localStorage.userInfo) {
                          if (!addresse) { return "--" }
                          return addresse
                        } else {
                          return "******"
                        }
                      })()
                    }

                  </div>
                  <CopyToClipboard text={addresse}
                    onCopy={() => {
                      this.setState({ copied: true })
                      openNotificationWithIcon("opne-success", "", '')
                    }}>
                    <Button className="bglanse" style={{ width: 80 }} type="primary">< FormattedMessage id="copy" defaultMessage={''} /></Button>
                  </CopyToClipboard>
                  {/* <Button type="primary" style={{ width: 80 }}></Button> */}
                </div>
                <div className="my-yaoqingma clear">
                  <div className="span-my-1">
                    < FormattedMessage id="My_exclusive_invitation" defaultMessage={''} />
                    :
                    </div>
                  <div className="span-my-5">
                    {
                      (() => {
                        if (localStorage.userInfo) {
                          return address
                        } else {
                          return "******"
                        }
                      })()
                    }

                  </div>
                  {
                    (() => {
                      if (localStorage.userInfo) {
                        return <div>
                          <CopyToClipboard text={address}
                            onCopy={() => {
                              this.setState({ copied: true })
                              openNotificationWithIcon("opne-success", "", '')                            }}>
                            <Button className="bglanse" style={{ width: 80 }} type="primary">< FormattedMessage id="copy" defaultMessage={''} /></Button>
                          </CopyToClipboard>
                          <div className="span-my-6" style={{ fontSize: 16 }}>
                            < FormattedMessage id="or" defaultMessage={''} />


                          </div>
                          <Button onClick={this.pictureSharing} className="bglanse" type="primary" style={{ width: 160 }}>
                            <img src={imgArr.a5} alt="" />
                            < FormattedMessage id="Picture_sharing" defaultMessage={''} />

                          </Button>
                        </div>
                      } else {
                        return <div>
                          <Button type="primary" style={{ width: 120 }} onClick={() => history.push('/login')}>
                            < FormattedMessage id="Sign_in" defaultMessage={''} /> /< FormattedMessage id="register" defaultMessage={''} />
                          </Button>
                        </div>
                      }
                    })()
                  }

                </div>
              </div>
            </div>
          </section>
          <Modal
            className="pictureSharing"
            title=""
            visible={this.state.pictureSharing}
            onOk={this.pictureSharingOk}
            onCancel={this.pictureSharingCancel}
            okText={< FormattedMessage id="Save_to_local" defaultMessage={''} />}
          >

            <ShareImage pictureSharingCancel={this.pictureSharingCancel} addresse={address} ref="shareimg" className="tupianxiazai "></ShareImage>
          </Modal>
          <section>
            <div className="content-box">
              {
                (() => {
                  if (localStorage.userInfo) {
                    return <div className="tabe-box clear">
                      <div className="content-box-a  content-box-c">
                        <div className="content-table">
                          <div className="content-table-title">
                            < FormattedMessage id="Invitation_record" defaultMessage={''} />

                            <span className="span">
                              < FormattedMessage id="Total_number_of_invitations" defaultMessage={''} />
                              
                              <span>{dataLength ? dataLength + " " : "0 "}</span> < FormattedMessage id="people" defaultMessage={''} /></span>
                          </div>
                          <TableForm huodongweituo={this.fenyeds} columns={columns} data={getInviteUser} dataLength={dataLength}></TableForm>
                          {dangqianchipang(dataLength)}
                        </div>
                      </div>
                      <div className="content-box-a content-box-c content-box-d">
                        <div className="content-table">
                          <div className="content-table-title">
                            < FormattedMessage id="Return_record" defaultMessage={''} />

                            <span className="span">
                              < FormattedMessage id="Total_Commission" defaultMessage={''} />
                              <span>{sumAmt ? sumAmt + " " : "0 "}</span>BTC   </span>
                          </div>
                          <TableForm huodongweituo={this.fenyed} columns={columns1} data={getRakeBack} dataLength={getRakeBacklength}></TableForm>
                          {dangqianchipang(getRakeBacklength)}
                        </div>
                      </div>
                    </div>
                  }
                })()
              }
              <div className="content-box-a ">
                <div className="content-table">
                  <div className="content-table-title">
                    {this.state.nian}< FormattedMessage id="year" defaultMessage={''} />{this.state.yue}< FormattedMessage id="month" defaultMessage={''} />
                    < FormattedMessage id="Invitation_list" defaultMessage={''} />

                  </div>
                  <div className="content-table-theerder clear">
                    <div className="tr">
                      < FormattedMessage id="ranking" defaultMessage={''} />
                    </div>
                    <div className="tr">
                      < FormattedMessage id="Account_no_of_the_inviting_party" defaultMessage={''} />
                    </div>
                    <div className="tr">
                      < FormattedMessage id="Reversion_of_commission" defaultMessage={''} />
                    </div>
                  </div>
                  {
                    getSortRakeBack.map((item, index) => {
                      console.log(index,'=========')
                      if(index*1>=3){
                        return false
                      }
                      return <div className="content-table-td clear" key={index + item}>
                        <div className="tr"><img src={(() => {
                          switch (item.sort) {
                            case "1": return imgArr.a6;
                            case "2": return imgArr.a7;
                            case "3": return imgArr.a8;
                            default:
                              return item.sort;
                              break;
                          }
                        })()} alt="" /></div>
                        <div className="tr">{
                          (() => {
                            if (item.account.indexOf("@") == -1) {
                              return <span>{item.account.substr(0, 3) + "****" + item.account.substr(-4)}</span>
                            } else {
                              let a = item.account.replace(/\"/g, "")
                              return <div>
                                <span>
                                  {a.substr(0, 3) + "****"}
                                </span>
                                <span>
                                  {a.split("@")[1]}
                                </span>
                              </div>
                            }
                          })()
                        }</div>
                        <div className="tr">{
                          item.sum_amt + " "
                        }BTC</div>
                      </div>
                    })
                  }
                </div>
              </div>
              {dangqianchipang(getSortRakeBack.length, "token")}
              <div className="content-box-b content-box-a">
                <div className="content-table">
                  <div className="content-table-title">
                    < FormattedMessage id="Activity_rules" defaultMessage={''} />
                  </div>
                  <p >
                    1
                  < FormattedMessage id="Afte_a_friend_accepts_the_invitation" defaultMessage={''} />
                  </p>
                  <div>
                    2
                    < FormattedMessage id="In_the_form_of_return_commission" defaultMessage={'30%'} />
                  </div>
                  <div>
                    3
                    < FormattedMessage id="The_statistics_of_the_day_when" defaultMessage={' =  *  * '} />
                  </div>
                  <div>
                    4
                    < FormattedMessage id="The_invitee_can_get_a_discoun" defaultMessage={'95'} />
                  </div>
                  <div>
                    5
                  < FormattedMessage id="The_effective_duration_of_the" defaultMessage={'90'} />
                  </div>
                  <div>
                    6
                    < FormattedMessage id="The_platform_will_take_the_market" defaultMessage={'5USDT'} />
                  </div>
                  <div>
                    7
                    < FormattedMessage id="Only_last_months_data_can_be" defaultMessage={'1'} />
                  </div>
                  <div>
                    8
                    < FormattedMessage id="If_the_invitee_violates_the_risk" defaultMessage={''} />
                  </div>
                  <p>
                    < FormattedMessage id="In_case_of_any_adjustment_to_the" defaultMessage={'GTEGTE.IO'} />
                  </p>
                </div>
              </div>
            </div>
          </section>
        </article>
        <Footer></Footer>
      </div>
    );
  }
}

export default fanyongBanner;